.movie-detail-card {
    width: 100%;
    height: 100%;
    background-color: #848c8e;
    color: black;
    font-weight: bold;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.single-movie-poster {
    width: 15em;
    height: 20em;
    border: 2px solid #f5b700;
    border-radius: 5px;
}

.single-movie-title {
    font-size: xxx-large;
    margin: 15px;
}

.rating {
    font-size: large;
}

.button {
    font-family: "Orbitron", sans-serif;
    font-size: large;
    width: 300px;
    height: 75px;
    border-radius: 8px;
    background-color: #f5b700;
    color: black;
    margin: 15px;
    cursor: pointer;
}

.image-container {
    width: 75%;
    height: 600px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 25px;
    background-position: center 25px;
    background-size: cover; 
  background-repeat: no-repeat;
}

.overview {
    margin-right: 100px;
    margin-left: 100px;
}

.tagline {
    margin: 10px;
}

@media screen and (max-width: 768px) {
    .image-container {
        width: 15em;
        height: 20em;
    }

    .single-movie-poster {
        width: 9em;
        height: 12em;
    }

    .button {
        width: 6em;
        height: 4em;
    }
}

@media screen and (min-width: 1024px) {
    .image-container {
        width: 40em;
        height: 40em;
    }

    .single-movie-poster {
        width: 25em;
        height: 30em;
    }

    .button {
        width: 12em;
        height: 5em;
    }

    .tagline, .release-date, .overview, .budget, .revenue, .runtime {
        font-size: x-large;
    }
}


